import React from "react";
import Button from "@material-ui/core/Button";

const Product = ({ id, img, title, price, desc, onClick }) => (
  <div>
    <img
      style={{
        maxWidth: "100%",
        maxHeight: "100%"
      }}
      src={img}
    />
    <h2>{title}</h2>
    <p>Price: {price} $</p>
    <p>{desc}</p>
    <Button
      variant="contained"
      color="primary"
      onClick={() => onClick({ id, img, title, price })}
    >
      Buy
    </Button>
  </div>
);

export default Product;
