import React from "react";
import PropTypes from "prop-types";
import { withStyles } from "@material-ui/core/styles";
import Paper from "@material-ui/core/Paper";
import Grid from "@material-ui/core/Grid";
import Header from "./header";
import List from "@material-ui/core/List";
import ListItem from "@material-ui/core/ListItem";
import ListItemSecondaryAction from "@material-ui/core/ListItemSecondaryAction";
import ListItemText from "@material-ui/core/ListItemText";
import Avatar from "@material-ui/core/Avatar";
import IconButton from "@material-ui/core/IconButton";
import DeleteIcon from "@material-ui/icons/Delete";
import Select from "react-select";
import Button from "@material-ui/core/Button";
import DatePickers from "./date-picker";

const styles = theme => ({
  root: {
    flexGrow: 1
  },
  paper: {
    padding: theme.spacing.unit * 2,
    textAlign: "center",
    color: theme.palette.text.secondary
  }
});

const busStations = [
  { value: "central", label: "Central" },
  { value: "zahid", label: "Zahid" }
];

const deliveryTime = [
  { value: "800", label: "8:00" },
  { value: "900", label: "9:00" },
  { value: "1000", label: "10:00" }
];

function CenteredGrid(props) {
  const { classes, onClick, products } = props;

  const total = products.reduce(
    (acc, product) => acc + product.amount * product.price,
    0
  );

  return (
    <div className={classes.root}>
      <Grid container spacing={24}>
        <Header classes={classes} />

        <Grid item xs={12}>
          <Paper className={classes.paper}>
            <h3>My cart</h3>
          </Paper>
        </Grid>
        <Grid item xs={12}>
          {products.length ? (
            <div>
              <Paper className={classes.paper}>
                <List>
                  {products.map(({ id, title, img, amount, price }) => (
                    <ListItem
                      key={id}
                      dense
                      button
                      className={classes.listItem}
                    >
                      <Avatar alt="Remy Sharp" src={img} />
                      <ListItemText primary={title} />
                      <ListItemText primary={`${price} $`} />
                      <ListItemText primary={amount} />
                      <ListItemSecondaryAction>
                        <IconButton
                          className={classes.button}
                          aria-label="Delete"
                        >
                          <DeleteIcon />
                        </IconButton>
                      </ListItemSecondaryAction>
                    </ListItem>
                  ))}
                  <p>Total: {total} $</p>
                </List>
              </Paper>
              <Paper className={classes.paper}>
                <DatePickers />
                <p>Bus stations near you</p>
                <Select options={busStations} />
                <p>Delivery time</p>
                <Select options={deliveryTime} />
                <br />
                <Button variant="contained" color="primary">
                  Checkout
                </Button>
              </Paper>
            </div>
          ) : (
            <Paper className={classes.paper}>Your cart is empty</Paper>
          )}
        </Grid>
      </Grid>
    </div>
  );
}

CenteredGrid.propTypes = {
  classes: PropTypes.object.isRequired
};

export default withStyles(styles)(CenteredGrid);
