import React, { Component } from "react";
import "./App.css";
import CenteredGrid from "./components/centered-grid";
import Cart from "./components/cart";

export default class App extends Component {
  state = {
    page: "main",
    products: [] // get from localstorage later
  };

  handleCartClick = () => {
    this.setState({
      page: "cart"
    });
  };

  handleDriverBtnClick = () => {
    this.setState({
      page: "driver"
    });
  };

  handleBuy = ({ id, img, title, price }) => {
    this.setState(
      prevState => {
        const hasSameId = item => +item.id === +id;
        const hasNotSameId = item => +item.id !== +id;
        const productFound = prevState.products.find(hasSameId);

        if (productFound) {
          productFound.amount++;
          return {
            products: [...prevState.products.filter(hasNotSameId), productFound]
          };
        } else {
          return {
            products: [
              ...prevState.products,
              { id, img, title, price, amount: 1 }
            ]
          };
        }
      },
      () => console.log("save to localstorage")
    );
  };

  render() {
    const { page, products } = this.state;

    switch (page) {
      case "main":
        return (
          <CenteredGrid
            onCartClick={this.handleCartClick}
            onProductClick={this.handleBuy}
            onDriverBtnClick={this.handleDriverBtnClick}
          />
        );
      case "cart":
        return <Cart products={products} />;
      case "driver":
        return "Driver Page";
      default:
        return null;
    }
  }
}
